@import "./_variables";
@import "./_mixins";

.map {
    iframe {
        width: 100%;
        height: 540px;
    }

    @include breakpoint-down('desktop') {
        iframe {
            height: 460px;
        }
    }

    @include breakpoint-down('laptop') {
        iframe {
            height: 400px;
        }
    }
}